.type {
  white-space: nowrap;
  overflow: hidden;
  position: absolute;
  bottom: 262px;
  left: 60px;
  z-index: 9;
  font-family: 'ITC Stone Serif Bold';
  font-weight: normal;
  transform-origin: top left;
}
.open-bracket,
.close-bracket {
  position: relative;
  top: -2px;
  font-size: 28px;
  margin: 0 3.5px 0 3.5px;
}
.type-letter-first-letter {
  font-size: 28px;
}
.type-letter {
  display: inline-block;
  font-size: 24px;
  letter-spacing: 0.3px;
}
.slash-type {
  position: relative;
  top: -1px;
  font-size: 27px;
  margin: 0 3px 0;
}
.type.L {
  transform: scale(1.1);
}
.type.VT {
  transform: scale(0.9);
}
.type.yes-it-is-PL{
  bottom: 231px;
}