$color-grey--light: #2f3136;
.about-wrap {
  font-family: 'Play', sans-serif;
  line-height: 1.5;
  font-size: 1rem;
  width: 80%;
  max-width: 200vh;
  margin: 3rem auto;
  text-align: justify;
  color: white;
  > * {
    margin: 1rem 0;
  }
  a {
    &:link,
    &:visited,
    &:active {
      color: #23a6d5;
    }
    &:hover {
      color: white;
    }
  }
  h1 {
    font-size: 3rem;
    background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
    background-size: 400% 400%;
    animation: gradient 5s ease infinite;
    text-align: center;
  }
  h2 {
    font-size: 2rem;
    font-weight: bold;
  }
  .linkz-for-contact {
    a {
      margin-right: 1rem;
    }
  }
  .bold {
    font-weight: bold;
  }
  ul {
    list-style: none;
    li:before {
      content: '+';
      display: inline;
      padding-right: 1rem;
    }
  }
  .credit-box a {
    &:link,
    &:visited,
    &:active {
      color: #e73c7e;
    }
  }
}
