.text-box {
  font-weight: normal;
  width: 681px;
  height: 150px;
  position: absolute;
  left: 66px;
  bottom: 115px;
  z-index: 9;
  text-align: justify;
  white-space: pre-line;
  font-family: 'Matrix Book';
  letter-spacing: -0.5px;
  transform-origin: left;
  &.text-Normal {
    font-family: 'ITC Stone Serif Medium Italic';
  }
  &.text-Spell,
  &.text-Trap,
  &.text-TokenII {
    bottom: 140px !important;
  }
  &.desc-size-L {
    font-size: 29px;
    line-height: 30px;
    bottom: 110px;
  }
  &.desc-size-M {
    font-size: 27px;
    line-height: 26px;
  }
  &.desc-size-S {
    font-size: 25px;
    line-height: 26px;
  }
  &.desc-size-T {
    font-size: 21px;
    line-height: 22px;
  }
  &.desc-size-VT {
    font-size: 17px;
    line-height: 17px;
  }
  &.yes-it-is-PL {
    bottom: 84px !important;
  }
}
