$color-grey--light: #2f3136;
.err404-wrap {
  font-family: 'Play', sans-serif;
  line-height: 1.5;
  height: 100vh;
  @media (max-aspect-ratio: 3/2), (aspect-ratio: 3/2) {
    height: calc(100vh - 4rem);
  }
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 80%;
  max-width: 200vh;
  margin: 0 auto;
  text-align: justify;
  color: white;
  padding: 1rem;
  h1 {
    position: relative;

    &::after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 2px;
      background: linear-gradient(90deg, #ee7752, #e73c7e);
    }
  }
  > * {
    margin: 1rem;
  }
  a {
    &:link,
    &:visited,
    &:active {
      color: #23a6d5;
    }
    &:hover {
      color: white;
    }
  }
}
