$color-grey--light: #2f3136;
.downloads-page-wrap {
  font-family: 'Play', sans-serif;
  line-height: 1.5;
  width: 90%;
  max-width: 200vh;
  margin: 3rem auto;
  text-align: justify;
  color: white;
  > * {
    margin: 1rem;
  }
  a {
    &:link,
    &:visited,
    &:active {
      color: #23a6d5;
    }
    &:hover {
      color: white;
    }
  }
  span {
    font-size: 1rem;
  }
  h1 {
    font-size: 3rem;
    text-align: center;
    background: linear-gradient(90deg, #ee7752, #e73c7e);
    background-clip: text;
    -webkit-background-clip: text;
    text-fill-color: transparent;
    -webkit-text-fill-color: transparent;
  }
  h2 {
    font-size: 2rem;
    font-weight: bold;
  }
  h3 {
    font-size: 1rem;
    font-weight: bold;
  }
  ul {
    list-style: none;
    li:before {
      content: '+';
      display: inline;
      padding-right: 1rem;
    }
  }
  .dl-card {
    background-color: #202225;
    padding: 1rem 2rem 1rem 1rem;
    min-height: 24rem;
    border-radius: 0.5rem;
    position: relative;
    box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.22);
    &::after {
      content: '';
      width: 4px;
      height: 100%;
      position: absolute;
      top: 0;
      right: 1rem;
      background: linear-gradient(0deg, #23a6d5, #23d5ab);
    }
    div {
      display: inline-block;
      padding: 1rem;
      margin: 0 0 1rem 0;
      border-radius: 0.5rem;
      color: black;
      background: white;
    }
    .dl-link {
      padding: 1rem 0;
    }
  }
}
