.title-text {
  position: absolute;
  display: inline-block;
  white-space: nowrap;
  top: 72px;
  left: 62px;
  z-index: 4;
  font-family: 'Matrix Regular Small Caps';
  font-size: 90px;
  font-weight: normal;
  line-height: 46px;
  transform-origin: left;
}
._title-fix-at {
  position: relative;
  font-size: 90px;
  font-weight: normal;
  line-height: 46px;
}
._title-fix-at::after {
  content: '' url('./at.svg');
  position: absolute;
  left: 0;
  top: 18px;
  transform-origin: top left;
  transform: scale(0.1);
}
