$primary: #11998e;
$secondary: #38ef7d;
$white: #fff;
$gray: #9b9b9b;
$c_primary-dark-hard: #202225;
.input-wrap {
  @media (min-aspect-ratio: 3/2), (aspect-ratio: 3/2) {
    height: 80vh;
    overflow-y: scroll;
  }
  @media (max-aspect-ratio: 3/2) {
    height: fit-content;
  }
  padding: 1rem 0;
}
.input__group {
  font-family: 'Play', sans-serif;
  position: relative;
  padding: 15px 0 0;
  margin: 10px auto 0;
  width: 90%;
  textarea {
    resize: vertical;
  }
  input,
  textarea {
    font-family: inherit;
    width: 100%;
    border: 0;
    border-bottom: 2px solid $gray;
    outline: 0;
    font-size: 1.3rem;
    color: $white;
    padding: 7px 0;
    background: transparent;
    transition: border-color 0.2s;
    &:required,
    &:invalid {
      box-shadow: none;
    }
    &::placeholder {
      color: transparent;
    }
    &:placeholder-shown ~ .lbl {
      font-size: 1.3rem;
      cursor: text;
      top: 20px;
    }
    &:focus {
      ~ .lbl {
        position: absolute;
        top: 0;
        display: block;
        transition: 0.2s;
        font-size: 1rem;
        color: $primary;
      }
      padding-bottom: 6px;
      border-width: 3px;
      border-image: linear-gradient(to right, $primary, $secondary);
      border-image-slice: 1;
    }
  }
  .lbl {
    position: absolute;
    top: 0;
    display: block;
    transition: 0.2s;
    font-size: 1rem;
    color: $gray;
  }
}
.input__group-type {
  font-family: 'Play', sans-serif;
  position: relative;
  padding: 15px 0 0;
  margin: 10px auto 0;
  width: 90%;
  display: flex;
  flex-flow: row nowrap;
  input {
    font-family: inherit;
    width: calc((100% - 3rem) / 4);
    //flex: 1;
    border: 0;
    border-bottom: 2px solid $gray;
    outline: 0;
    font-size: 1.3rem;
    color: $white;
    padding: 7px 0;
    background: transparent;
    transition: border-color 0.2s;
    &:not(:last-child) {
      margin: 0 1rem 0 0;
    }
    &:required,
    &:invalid {
      box-shadow: none;
    }
    &::placeholder {
      color: $gray;
      & ~ .lbl {
        top: 0;
      }
    }
    &:focus {
      padding-bottom: 6px;
      border-width: 3px;
      border-image: linear-gradient(to right, $primary, $secondary);
      border-image-slice: 1;
    }
  }
}
.custom-select {
  position: relative;
  display: block;
  width: 90%;
  margin: 25px auto;
  //border: 1px solid #3C1C78;
  //-color: #16013E;
  z-index: 1;
  select {
    font-family: 'Play', sans-serif;
    font-size: 1.3rem;
    border: none;
    outline: none;
    border-bottom: 2px solid;
    border-image: linear-gradient(to right, $gray, $gray);
    border-image-slice: 1;
    background: transparent;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    border-radius: 0;
    margin: 0;
    display: block;
    width: 100%;
    padding: 0.5rem 3rem 1rem 0;
    text-indent: -1px;
    color: $gray;
    margin: -0.5rem 0 -1rem;
    option {
      font-family: inherit;
      font-size: inherit;
    }
    &:focus {
      color: $primary;
      border-bottom: 3px solid;
      border-image: linear-gradient(to right, $primary, $secondary);
      border-image-slice: 1;
    }
  }
  &:after {
    position: absolute;
    right: 0;
    top: 0;
    width: 50px;
    height: 100%;
    line-height: 38px;
    content: '';
    background: url('../../shared/svgs/Chevron_down_font_awesome.svg') center
      no-repeat;
    background-size: 40%;
    text-align: center;
    color: $gray;
    font-size: 24px;
    z-index: -1;
    filter: invert(50%);
  }
}
.atk-def {
  display: flex;
  flex-flow: row nowrap;
  width: 90%;
  margin: 0 auto;
  & > div:first-child {
    margin-right: 1rem;
  }
}
.atk-link {
  width: 90%;
  margin: 0 auto;
  div {
    width: 100%;
  }
}
.glow {
  background-color: skyblue;
}
.txt-size {
  font-family: 'Play', sans-serif;
  font-size: 1.3rem;
  color: $gray;
  padding: 15px 0 0;
  margin: 10px auto 0;
  width: 90%;
  .act {
    border-color: $white;
    color: $c_primary-dark-hard;
    background-color: $white;
  }
  @media (min-aspect-ratio: 1/1) {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    flex: 1;
    button {
      flex-grow: 1; /* default 0 */
      margin: 0 1rem;
      &:last-child {
        margin-right: 0;
      }
    }
  }
  @media (max-aspect-ratio: 1/1), (aspect-ratio: 1/1) {
    display: flex;
    flex-direction: column;
    button {
      margin: 1rem 0 0;
    }
  }
}
button[class^='bttn-txt'] {
  color: $white;
  font-family: 'Play', sans-serif;
  cursor: pointer;
  z-index: 1;
  text-decoration: none;
  -webkit-transition: 0.3s all ease;
  transition: 0.3s ease all;
  background-color: transparent;
  text-transform: uppercase;
  display: inline-block;
  text-align: center;
  outline: none;
  border: 3px solid $gray;
  border-radius: 2px;
  &:hover {
    border-color: $white;
    color: $c_primary-dark-hard;
    background-color: $white;
  }
}
.scale-slider {
  font-family: 'Play', sans-serif;
  font-size: 1.3rem;
  color: $gray;
  width: 90%;
  margin: 1rem auto 0;
  display: flex;
  @media (min-aspect-ratio: 1/1) {
    align-items: center;
  }
  @media (max-aspect-ratio: 1/1), (aspect-ratio: 1/1) {
    align-items: initial;
    flex-direction: column;
    input {
      margin: 1rem 0 0 !important;
    }
  }
  input[type='range'] {
    flex: 1;
    margin: 0 0 0 1rem;
  }
}
.hint-txt {
  font-family: 'Play', sans-serif;
  font-size: 1.3rem;
  color: #F90;
  width: 90%;
  margin: 0 auto;
}
.is-p {
  width: 90%;
  padding: 1rem 0 0;
  margin: 0 auto;
  font-family: 'Play', sans-serif;
  font-size: 1.3rem;
  color: $primary;
  display: flex;
  align-items: center;
  justify-content: space-between;
}