._p-l,
._p-r {
  position: absolute;
  width: 60px;
  height: 50px;
  z-index: 9;
  color: #000;
  font-family: 'Matrix Bold Small Caps';
  font-size: 3.6rem;
  line-height: 3.3rem;
  justify-content: center;
  text-align: center;
  align-items: center;
  &.M {
    bottom: 326px;
  }
  &.S {
    bottom: 304px;
  }
  &.L {
    bottom: 310px;
  }
}
._p-l {
  left: 53px;
  }
._p-r {
  right: 52px;
}
.text-box-p {
  font-weight: normal;
  width: 554px;
  height: 124px;
  position: absolute;
  left: 130px;
  z-index: 9;
  text-align: justify;
  white-space: pre-line;
  font-family: 'Matrix Book';
  letter-spacing: -0.5px;
  transform-origin: left;
  &.desc-size-L {
    font-size: 29px - 1px;
    line-height: 30px - 1px;
  }
  &.desc-size-M {
    font-size: 27px - 1px;
    line-height: 26px - 1px;
  }
  &.desc-size-S {
    font-size: 25px - 1px;
    line-height: 26px - 1px;
  }
  &.desc-size-T {
    font-size: 21px - 1px;
    line-height: 22px - 1px;
  }
  &.S {
    bottom: 268px;
  }
  &.M {
    bottom: 312px;
  }
  &.L {
    bottom: 310px;
  }
}