.card-preview {
  width: 813px;
  height: 1185px;
  position: relative;
  overflow: hidden;
}
#myCanvas {
  transform-origin: top left;
  transform: scale(1);
  max-height: 80vh;
  max-width: calc(80vh * (813 / 1185));
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  user-select: none;
}
._init#myCanvas {
  margin: 0 auto;
}
._DLR#myCanvas {
  margin: 0 0 0 0;
}
